<template>
  <v-card class="mx-auto" width="100%" height="100%">
    <v-navigation-drawer permanent width="100%">
      <!-- 个人信息开始 -->
      <v-list>
        <v-list-item>
          <v-list-item-avatar>
            <v-img :src="require('@/assets/img/user2.jpg')"></v-img>
          </v-list-item-avatar>
        </v-list-item>

        <v-list-item link>
          <v-list-item-content>
            <v-list-item-title class="title">Rodsen</v-list-item-title>
            <v-list-item-subtitle>rodsen1995@gmail.com</v-list-item-subtitle>
          </v-list-item-content>

          <v-list-item-action>
            <v-icon>mdi-menu-down</v-icon>
          </v-list-item-action>
        </v-list-item>
      </v-list>
      <!-- 个人信息结束 -->
      <v-divider></v-divider>
      <!-- 导航列表开始 -->
      <v-list nav dense>
        <v-list-item-group v-model="selectedItem" color="primary">
          <v-list-item v-for="(item, i) in items" :key="i" @click="showPopup">
            <v-list-item-icon>
              <v-icon v-text="item.icon"></v-icon>
            </v-list-item-icon>

            <v-list-item-content>
              <v-list-item-title v-text="item.text"></v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </v-list-item-group>
      </v-list>
      <!-- 导航列表结束 -->
    </v-navigation-drawer>
  </v-card>
</template>
<script>
export default {
  data: () => ({
    selectedItem: 0,
    items: [
      { text: "主页", icon: "mdi-folder" },
      { text: "开源项目", icon: "mdi-account-multiple" },
       { text: "媒体资源", icon: "mdi-history" },
      { text: "技术学习", icon: "mdi-star" },
      { text: "关于", icon: "mdi-check-circle" },
    ],
    linkPath: ["Home","Project", "Media",  "Study", "About"],
  }),
  watch: {
    selectedItem: function () {
      if (this.selectedItem !== undefined) {
        const pathName = `${this.linkPath[this.selectedItem]}`;
        // console.log(this.$route.name)
        if (pathName !== this.$route.name) {
          this.$router.push({ name: pathName });
        }
      }
    },
  },
  props: {
    showPopup: {
      type: Function,
    },
  },
};
</script>
<style>
.mx-auto .v-list-item-group .v-list-item--active {
  color: #385d72;
}
</style>