<template>
  <div class="play">
    <div class="main">
      <!-- 隐藏的音频开始 -->
      <audio
        ref="a"
        v-show="false"
        src="../../../../public/hidden.mp3"
        controls
      >
        <p>后备内容</p>
      </audio>
       <!-- 隐藏的音频结束 -->
      <img src="../../../assets/img/music1.webp" alt="" />
      <div class="control">
        <van-icon name="arrow-left" />
        <van-icon
          size="40"
          v-show="!play"
          name="play-circle-o"
          @click="controlPlay"
        />
        <van-icon
          size="40"
          v-show="play"
          name="pause-circle-o"
          @click="controlPlay"
        />
        <van-icon name="arrow" />
      </div>
      <div class="btns">
        <van-icon name="like-o" /><van-icon name="volume" /><van-icon
          name="bars"
        />
      </div>
    </div>
    <div class="song-msg">
      <span class="name">Fuhrahisha-</span>
      <span class="author">Atomic Drum Assembly</span>
    </div>
  </div>
</template>
<script>
// const audio = new Audio();
// audio.src = "../../../../public/hidden.mp3";
// audio.play();
export default {
  data: () => ({
    play: false,
    audio: {},
  }),
  methods: {
    // 播放控制
    controlPlay() {
      this.play = !this.play;
      if (this.play) {
        this.audio.play();
      } else {
        this.audio.pause();
      }
    },
   
  },
  mounted() {
    // audio.play();
    this.audio = this.$refs.a;
  },
};
</script>
<style>
@import url("./Play.css");
</style>
