<template>
  <v-card class="tab">
    <!-- tab标题开始 -->
    <v-tabs
      fixed-tabs
      v-model="tab"
      background-color="deep-purple accent-4"
      centered
      icons-and-text
    >
      <v-tabs-slider></v-tabs-slider>

      <v-tab :href="`#tab-${index+1}`" v-for="(item,index) in tabData" :key="index"> {{item}} </v-tab>
    </v-tabs>
    <!-- tab标题结束 -->
    <!-- tab内容开始 -->
    <v-tabs-items v-model="tab">
      <v-tab-item value="tab-1">
        <v-card flat>
            <slot name="space1"></slot>
        </v-card>
      </v-tab-item>
      <v-tab-item  value="tab-2">
        <v-card flat>
            <slot name="space2"></slot>
        </v-card>
      </v-tab-item>
      <v-tab-item  value="tab-3">
        <v-card flat>
            <slot name="space3"></slot>
        </v-card>
      </v-tab-item>
    </v-tabs-items>
    <!-- tab内容结束 -->
  </v-card>
</template>
<script>
export default {
  data() {
    return {
      tab: null,
    };
  },
  props:{
    tabData:{
      type:Array,
      default:()=>([])
    }
  }
};
</script>
<style>
.tab .v-slide-group__wrapper {
  height: 37px;
}

.tab .v-tabs {
  height: 40px;
}

.tab .v-tab:before {
  background-color: #fff;
}

.tab .v-tabs-slider {
  background-color: #385D72;
}

</style>