import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home/Home.vue'
import Study from '../views/Study/Study.vue'
import Project from '../views/Project/Project.vue'
import Media from '../views/Media/Media.vue'
import About from '../views/About/About.vue'

Vue.use(VueRouter)

const routes = [
  {
    path:'/',
    redirect: { name: 'Home' }
  },
  {
    path: '/Home',
    name: 'Home',
    component: Home
  },
  {
    path: '/Study',
    name: 'Study',
    component: Study
  },
  {
    path: '/Project',
    name: 'Project',
    component: Project
  },
  {
    path: '/Media',
    name: 'Media',
    component: Media
  },
  {
    path: '/About',
    name: 'About',
    component: About
  }
]

const router = new VueRouter({
  routes
})

export default router
