<template>
  <div>
    <van-popup v-model="show" position="left" @click-overlay='showPopup'> 
       <slot></slot>
    </van-popup>
  </div>
</template>
<script>
export default {
  data: () => ({
    show: false,
  }),

  watch: {
    //   根据isPop值来更新show的值
    isPop: function () {
      if (this.isPop) {
        this.show = true;
      }else {
          this.show = false
      }
    },
   
  },
  methods: {},
  props: {
    isPop: {
      type: Boolean,
      default: false,
    },
    showPopup:{
        type:Function
    }
  },
 
};
</script>