<template>
  <div class="navbar">
    <van-nav-bar :title="title" @click-left="showPopup" fixed placeholder>
      <template #left>
        <van-icon size="25" color="#ffff" name="wap-nav" />
      </template>
    </van-nav-bar>
  </div>
</template>
<script>
export default {
  data: () => ({
    title: "",
    navMap: {
      Home: "Rodsen",
      Project: "开源项目",
      Study: "技术学习",
      Media: "媒体资源",
      About: "关于",
    },
  }),
  props: {
    showPopup: {
      type: Function,
    },
  },
  watch: {
    $route: "setTitle",
  },
  methods: {
    setTitle() {
      this.title = this.navMap[this.$route.name];
      // 根据页面设置不同样式
      if (this.title == "Rodsen") {
        this.addHomeStyle();
        this.setBg("#333333");
      } else if (this.title == "关于") {
        this.setBg("#E49486");
        this.resHomeStyle();
      } else if (this.title == "开源项目") {
        this.setBg("#1F2229");
        this.resHomeStyle();
      } else if (this.title == "技术学习") {
        this.setBg("#C0504D");
        this.resHomeStyle();
      } else if (this.title == "媒体资源") {
        this.setBg("#007D65");
        this.resHomeStyle();
      } else {
        this.resHomeStyle();
        this.setBg("#333333");
      }
    },
    addHomeStyle() {
      const navText = document.getElementsByClassName("van-nav-bar__title")[0];
      const nav = document.getElementsByClassName("van-nav-bar__content")[0];
      navText.className = "van-nav-bar__title van-ellipsis a";
      navText.style.fontWeight = "700";
      navText.style.fontSize = "1.5rem";
      nav.className = "van-nav-bar__content bottom-line";
    },
    resHomeStyle() {
      const navText = document.getElementsByClassName("van-nav-bar__title")[0];
      const nav = document.getElementsByClassName("van-nav-bar__content")[0];
      navText.className = "van-nav-bar__title van-ellipsis";
      navText.style.fontWeight = "500";
      navText.style.fontSize = "1rem";
      nav.className = "van-nav-bar__content";
    },
    setBg(color) {
      const nav = document.getElementsByClassName("van-nav-bar__content")[0];
      nav.style.backgroundColor = color;
    },

   
  },
  mounted() {
    this.setTitle();
   
  },
};
</script>
<style>
.navbar .van-nav-bar__content {
  /* background-color: #385D72; */
  background-color: #333333;
}
.navbar .van-nav-bar__title {
  color: aliceblue;
}

/* 首页样式 */
/* .navbar .van-nav-bar__title::first-letter {
  color: #ff7f27;
}
.navbar .van-nav-bar__title {
  font-weight: 700;
  font-size: 1.5rem;
} */
.a::first-letter {
  /* color: #ff7f27; */
  color: #b12222;
}
/* 首页导航底部边框 */
.bottom-line {
  border-bottom: 3px solid #b12222;
}
</style>